body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Arial', sans-serif;
  background-color: #fff; }

img {
  max-width: 100%;
  height: auto; }

.button {
  border: 2px solid #444444;
  color: #444444;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.zigzag {
  margin: 0 auto;
  height: 6px;
  width: 320px;
  background: url("../img/bg-zigzag-line.svg") repeat-x;
  background-size: 100% 6px; }

.container {
  padding-right: 30px;
  padding-left: 30px;
  margin: 0 auto; }

.inner-container {
  margin: 0 auto; }

@media (max-width: 768px) {
  .container {
    max-width: 260px; }
  .inner-container {
    max-width: 320px; } }

@media (min-width: 768px) {
  .container {
    width: 668px;
    padding-right: 50px;
    padding-left: 50px; }
  .inner-container {
    width: 768px; }
  .zigzag {
    height: 16px;
    width: 768px;
    background-size: 100% 16px; } }

@media (min-width: 1200px) {
