    width: 1050px;
    padding-right: 100px;
    padding-left: 100px; }
  .inner-container {
    width: 1050px; }
  .zigzag {
    height: 23px;
    width: 1050px;
    background-size: 100% 23px; } }

iframe {
  min-width: 320px;
  width: 100vw;
  border: none; }

