.copyright__text {
    @include font(18px, 19px);
}

.page-footer__logo {
    display: none;
}

.page-footer {
    background-color: #f1f1f1;
}

.social {
    @include indents();
}

.social__list {
    @include indents();
    margin: 0 auto;
    width: 141px;
    height: 28px;
    
    display: flex;
    list-style: none;
    justify-content: space-between;
    align-items: center;
}

.page-footer__social {
    
    margin: 0 auto;

    min-height: 80px;
    width: 260px;

    display: flex;
    align-items: center;
    border-bottom: 1px solid #e5e5e5;
}

.copyright {
    width: 182px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.copyright__text {
    @include font(19px, 19px, 700);
}

.page-footer__copyright {
    margin: 0 auto;

    min-height: 80px;
}