.contacts {
    position: relative;
}

.contacts__zigzag {
    margin-bottom: 40px;
}

.contacts__title {
    @include indents();
    @include font(18px, 30px, 700);
    margin-bottom: 35px;
}

.contacts__list {
    @include indents();
    @include font(15px, 30px);
    color: $grey;

    margin-bottom: 40px;
}

.contacts__adress {
    @include indents();
    padding-right: 20px;
    white-space: nowrap;
    vertical-align: top;   
}

.contacts__e-mail {
    display: block;
    width: 180px;
    border-bottom: 1px solid $dark-green;

    margin-bottom: 38px;
}

.contacts__value {
    @include indents();
    vertical-align: top; 
}

.contacts__map {
    margin-bottom: 130px;
}

.contacts__mail {
    @include font(18px, 20px);
    position: absolute;
    top: 762px;

    display: block;
    box-sizing: border-box;
    width: 260px;
    height: 52px;
    
    line-height: 48px;
    vertical-align: middle;
    text-align: center;
    text-transform: uppercase;
}

@media(min-width: $tablet-width) {
    .contacts__zigzag {
        margin-bottom: 64px;
    }
    
    .contacts__title {
        @include font(43px, 50px, 700);

        margin-bottom: 50px;
    }
    
    .contacts__list {
        @include font(17px, 30px);
    
        margin-bottom: 60px;
    }
    
    .contacts__adress {
        padding-right: 20px;  
    }
    
    .contacts__e-mail {
    
        margin-bottom: 38px;
    }
    
    .contacts__value {
        
    }
    
    .contacts__map {
        margin-bottom: 164px;
    }
    
    .contacts__mail {
        top: 753px;
    
        width: 260px;
        height: 52px;
        
    }
}